.Chat {
    height: 75%;
    width: 265px;
    scrollbar-width: 1px;
}
    .Chat .chat {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 0 5px;
        justify-content: space-between;
        padding: 0 15px;
    }
    .Chat .chatHeader {
        padding: 10px;
        text-align: center;
        padding-top: 0;
        
    }
    .Chat .sendMsgB {
        width: 30px;
        height: 30px;
        margin-left: 10px;
        color: white;
        font-weight: 600;
        background: #232531;
        border: none;
        border-radius: 5px;
    }
    .Chat .sendMsgB svg {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .Chat .chat .messages {
        overflow-y: scroll;
    }
    .Chat .chat .inputMessage {
        padding: 20px 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .Chat .chat .inputMessage .timer {
        margin-left: 10px;
    }
    .chat .inputMessage textarea {
        background: #232531;
        border: none;
        color: white;
        border-radius: 99px;
        padding: 6px 16px;
        resize: none;
        width: 180px;
        min-height: 30px;
        max-height: 200px;

    }
    .chat .inputMessage textarea:focus {
        outline: none;
    }
    .chat .message {
        align-items: center;
        border-radius: 5px;
        margin-bottom: 13px;
        padding: 0 5px;
    }
    .chat .message .sender {
        display: flex;
        align-items: center;
    }
    .chat .message .content {
        word-break: break-all;
        padding: 5px 5px;
    }
    .chat .message .content .text {
        margin: 0;
    }
    .chat .message .content .address {
        font-size: 15px;
        font-weight: 600;
        margin-right: 5px;
        color: white;
        text-decoration: none;
    }
    .chat .message .avatar {
        width: 35px;
        height: 35px;
        object-fit: contain;
    }