#coinFlip {
    position: relative;
    height: 90px;
    width: 90px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
#coinFlip .coin0 img, #coinFlip .coin1 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#coinFlip .coin0, #coinFlip .coin1 {
    position: absolute;
    width: 90px;
    height: 90px;
}

#coinFlip .coin0 {
    -webkit-transform: translateZ(1px);
            transform: translateZ(1px);
    border-radius: 50%;
}

#coinFlip .coin1 {
    -webkit-transform: translateZ(-1px) rotateY(180deg);
            transform: translateZ(-1px) rotateY(180deg);
    border-radius: 50%;
}

