.ActiveGames {
    text-align: center;
}
    .ActiveGames .gameTitle {
        
        color: #00FCFE;
        font-size: 50px;
    }
    .ActiveGames .typeWrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .ActiveGames .type {
        display: flex;
        align-items: center;
    }
    .ActiveGames .typeHeader {
        
        text-align: left;
        position: relative;
    }
    .ActiveGames .gamesCounter {
        color: #F94CCB;
        font-size: 16px;
        margin-top: 0;
        margin-left: 5px;
    }
    .ActiveGames .createGameB {
        padding: 10px 30px;
        background: rgba(249, 76, 203, 0.2);
        border: 2px solid #F94CCB;
        box-shadow: 0px 0px 5px 1px #F94CCB;
        color: #F94CCB;
        border-radius: 8px; 
        font-size: 18px;
        text-transform: uppercase;
        display: flex;
        align-items: center;
    }
    .ActiveGames .amounts {
        max-width: 300px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .ActiveGames .amountChosen,
    .ActiveGames .amount {
        width: 70px;
        padding: 10px 0;
        border: 2px solid rgba(0, 252, 254, 1);
        box-shadow: inset 0px 0px 5px 1px #00FCFE;
        border-radius: 4px;
        margin: 0 10px;
        margin-bottom: 10px;
        cursor: pointer;
    }
    .ActiveGames .amountChosen {
        border: 2px solid white;
        box-shadow: inset 0px 0px 5px 1px white;
    }
    .ActiveGames .createGameB svg {
        margin-left: 5px;
    }
    .ActiveGames .games {
        display: grid;
        grid-template-columns: repeat(auto-fit, 400px);
        align-items: center;
        justify-content: space-around;
        padding: 10px 15px;
        border: none;
    }
    .ActiveGames .gameWrapper {
        background: rgba(0, 255, 255, 0.1);
        border: 2.5px solid #00FCFE;
        box-shadow: 0px 0px 5px 1px #00FCFE;
        display:inline-block;
        padding: 12px;
        border-radius: 17px;
        margin-bottom: 20px;
        margin: 15px 5px;
        position: relative;
        
    }
    .ActiveGames .gameWrapper .closeB {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 10px;
        cursor: pointer;
        background: none;
        border: none;
        border-radius: 6px;
    }
    .ActiveGames .gameWrapper .move {
        width: 90px;
        height: 90px;
        object-fit: contain;
    }
    .ActiveGames .gameWrapper .movePlaceholder {
        width: 90px;
        height: 90px;
        margin-top: 8px;
        object-fit: contain;
    }
    .ActiveGames .gameWrapper .closeB svg {
        fill: #777;
        transition: fill 200ms ease 0s;
    }
    .ActiveGames .gameWrapper .info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 18px;
    }
    .ActiveGames .info .join {
        flex-direction: unset;
        text-align: center;
        align-items: center;
    }
    .ActiveGames .gameWrapper .bet {
        display: flex;
        align-items: center;
    }
    .ActiveGames .gameWrapper .bet svg {
        width: 25px;
        height: 25px;
        margin-left: 5px;
        object-fit: cover;
    }
    .ActiveGames .gameWrapper .bet h3 {
        margin: 0;
        font-size: 28px;
    }
    .ActiveGames .gameWrapper .deleteB {
        width: 38px;
        height: 38px;
        background-color: transparent;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 2px solid white;
        border-radius: 50%;
    }
    .ActiveGames .gameWrapper svg {
        width: 30px;
        height: 20px;
        object-fit: cover;
    }
    .ActiveGames .players {
        display: flex;
        align-items: center;
    }
    .ActiveGames .endedGame {
        -webkit-animation: remove 2s linear forwards; 
        animation: remove 2s linear forwards;
    }
    .ActiveGames .players .player {
        padding: 20px 20px;
        border-radius: 5px;
        height: 168px;
        width: 128px;
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
    }
    .ActiveGames .players .player .choice {
        position: absolute;
        width: 25px;
        height: 25px;
        object-fit: cover;
        top: 10px;
        right: 10px;
    }
    .ActiveGames .players .middle {
        width: 120px;
        height: 90px;
        font-size: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .ActiveGames .players .player .address {
        margin: 0;
        color: white;
        text-decoration: none;
        font-size: 15px;
        font-weight: 400;
    }
    .ActiveGames .modal .coinChooser {
        display: flex;
        width: 100%;
        justify-content: space-around;
        margin-bottom: 15px;
        cursor: pointer;
    }
    .ActiveGames .modal .coinChooser img {
        width: 75px;
        height: 75px;
        object-fit: cover;
        border: 3px solid transparent;
        border-radius: 50%;
    }
    .ActiveGames .modal .coinChooser .chosen {
        border: 3px solid white;
        border-radius: 50%;
    }
    .ActiveGames .players .loser {
        opacity: 0.6;
    }
    .ActiveGames .players .winner {
        border: 2px solid #bdba59;
    }
    .ActiveGames .players .crown {
        width: 50px;
        height: 50px;
        object-fit: cover;
        position: absolute;
        left: 10px;
        margin: 0 auto;
        transform: rotate(-45deg);
    }

@keyframes remove {
    from { 
        opacity: 1;
        display: flex;
    }
    99% {
        opacity: 0;
    }
    to { 
        display: none;
     }
}

@media (max-width: 880px) {
    .ActiveGames .games {
        display: flex;
        flex-direction: column;
    }
    .ActiveGames .gameWrapper {
        margin-right: 0;
        width: 100%;
    }
    .gameWrapper {
        margin-right: 0;
    }
    .ActiveGames .createGameB {
        padding: 10px;
    }
    .ActiveGames .recentGamesWrapper {
        margin-left: -12.5px;
    }
}
