@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(107,0,67);
  background: linear-gradient(156deg, rgb(68, 0, 43) 0%, rgba(1,0,4,1) 12%, rgba(1,0,4,1) 84%, rgb(68, 0, 43) 100%);
  background-repeat: no-repeat;
  background-size: contain;
  color: #f5f5f5f8;
  font-family: 'Orbitron', sans-serif;
  height: 100vh;
}

* {
  box-sizing: border-box;
}
/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.356);
  border-radius: 8px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

code {
  font-family: source-code-pcdro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
  font-family: 'Orbitron', sans-serif;
  font-weight: 500;
}
button:disabled {
  opacity: .5;
  cursor: default !important;
}
h1, h2, h3, h4, h5, p, a {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  user-select: none;
}
textarea,
input {
  font-family: 'Orbitron', sans-serif;
  font-weight: 500;
}
input:focus {
  outline: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

a {
  color: white;
  text-decoration: none;
}

.scroll-x::-webkit-scrollbar-thumb, .scroll-xy::-webkit-scrollbar-thumb, .scroll-y::-webkit-scrollbar-thumb {
  width: 6px;
  height: 6px;
  background-color: #333541;
  border-radius: 8px;
}


.Body {
  display: flex;
  padding-top: 105px;
  height: 100vh;
}

.Page {
    padding: 40px;
    padding-top: 20px;
    width: calc(100% - 550px);
    margin: 0 auto;
}

.Page::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.Page {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  overflow-y: scroll;
}

.box {
  background: rgba(255, 255, 255, 0.1);;
  padding: 10px 0;
  border: 1px solid rgba(0, 252, 254, 1);
  box-shadow: inset 0px 0px 5px 1px #00FCFE;
  border-radius: 40px;
}

.flip {
  transform: rotateY(180deg);
}

.actionB {
  padding: 10px 30px;
  background: rgba(249, 76, 203, 0.2);
  border: 2px solid #F94CCB;
  box-shadow: 0px 0px 5px 1px #f94ccb;
  color: #F94CCB;
  text-transform: uppercase;
  border-radius: 8px;
  font-size: 15px;
  margin: auto;
}

@media (max-width: 880px) {
  .Body {
    padding-top: 20px;
    height: calc(100vh - 110px);
  }
  .Page {
    padding: 20px;
    width: 100%;
  }
  .box {
    border: unset;
    box-shadow: unset;
    border-radius: unset;
  }
}