.RightSide {
    position: fixed;
    top: 120px;
    height: calc(100vh - 80px);
    right: 20px;
    width: 265px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 50px;
}

@media (max-width: 880px) {
    .RightSide {
        display: none;
    }
}