.Profile {
    text-align: center;
}

.Profile .rightInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 100;
    text-align: center;
    word-break: break-word;
}
.Profile .leftInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Profile .editB {
    padding: 10px 30px;
    background-color: #59BD7D;
    font-weight: 600;
    color: #F5F5F5;
    border: none;
    border-radius: 99px;
}

.Profile .gameStats {
    display: flex;
    justify-content: center;
    width: 90%;
    margin-top: 30px;
}

.gameStat .tag {
    color: white;
    font-size: 20px;
}

.Profile .statSeparator {
    
    color: #F94CCB;
    font-size: 40px;
}
.Profile .gameStat .counter {
    margin: 0;
    
    color: #F94CCB;
    font-size: 40px;
}
.Profile .profileBox {
    margin-bottom: 20px;
    padding: 40px;
    display: flex;
    align-items: center;
    width: 100%;
}

.profileBox .avatar {
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.Profile .username,
.Profile .address {
    display: flex;
}
.Profile .username .value,
.Profile .address .publicKey {
    margin: 0;
}

.Profile .address .publicKey {
    color: #00FCFE;
    font-size: 30px;
    
}

.Profile .address .scanLink {
    display: block;
    cursor: pointer;
    margin-left: 5px;
}

.Profile .statCircle {
    width: 75px;
    height: 75px;
    border: double 4px transparent;
    border-radius: 80px;
    background-origin: border-box;
    background-clip: content-box, border-box;
}
.Profile .address .scanLink img {
    width: 18px;
    height: 18px;
    object-fit: cover;
}



@media (max-width: 880px) {
    .Profile .profileBox {
        display: inline-block;
        word-break: break-all;
    }
    .Profile .rightInfo {
        margin-top: 25px;
    }
}