.Navbar {
    width: 100vw;
    height: 105px;
    position: fixed;
    z-index: 1;
}
.Navbar .navbar {
    padding: 25px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 110px;
}
.Navbar .mobile_user {
    display: none;
}
.wallet-adapter-button-trigger {
    background-color: #59BD7D;
}

.wallet-adapter-button-trigger {
    all: unset;
    padding: 10px 30px;
    background: rgba(249, 76, 203, 0.2);
    border: 2px solid #F94CCB;
    box-shadow: 0px 0px 5px 1px #f94ccb;
    color: #F94CCB;
    text-transform: uppercase;
    
    border-radius: 8px;
    font-weight: 600;
    font-size: 15px;
    margin: auto;
    cursor: pointer;
}
    .Navbar .left {
        display: flex;
        align-items: center;
    }
    .Navbar .left .navigation {
        margin-left: 10px;
    }
    .Navbar .left .navigation .link:hover {
        border-bottom: 2px solid #746bf5;
    }
    .Navbar .navLinks {
        padding-left: 15px;
    }
    .Navbar .navLinks .inactiveNavLink {
        color: #f5f5f5c1
    }
    .Navbar .navLinks .inactiveNavLink:hover {
        color: #F5F5F5
    }
    .Navbar .navLinks .activeNavLink {
        color: #F5F5F5
    }
    .Navbar .logo {
        display: flex;
        align-items: center;
        padding-right: 15px;
    }
    .Navbar .logo .title {
        margin: 0;
        margin-left: 10px;
        font-size: 24px;
        font-family: 'Squared';
        font-weight: 200;
        letter-spacing: 3px;
    }
    .Navbar .logo img {
        width: auto;
        height: 70px;
        object-fit: cover;
    }
    .Navbar .user {
        display: flex;
        align-items: center;

    }
    .Navbar .user .balance {
        height: 40px;
        display: flex;
        align-items: center;
        background: rgba(0, 252, 254, 0.1);
        border: 2.5px solid #00FCFE;
        box-shadow: inset 0px -4px 9px rgba(0, 252, 254, 0.5), inset 0px 4px 9px rgba(0, 252, 254, 0.5);
        border-radius: 99px;
        justify-content: center;
        padding: 15px 5px;
        margin-right: 15px;
    }
    .Navbar .user .balance svg {
        height: 20px;
        width: 20px;
        object-fit: cover;
    }
    .Navbar .user .balance .amount {
        padding: 5px;
    }
    .Navbar .user .balance .amount[data-balancechanged='1'] {
        animation: balanceIncrease 2.5s 1;
    }
    .Navbar .user .balance .amount[data-balancechanged='-1'] {
        animation: balanceDecrease 2.5s 1;
    }
    .Navbar .user .withdrawB,
    .Navbar .user .depositB {
        padding: 10px 30px;
        background-color: transparent;
        font-weight: 600;
        color: #F5F5F5;
        border: 2px solid #F5F5F5;
        margin-right: 15px;
        border-radius: 99px;
    }
    .Navbar .user .profile {
        display: flex;
        color: #F5F5F5;
        text-decoration: none;
        align-items: center;
        background: rgba(0, 252, 254, 0.1);
        border: 2.5px solid #00FCFE;
        box-shadow: inset 0px -4px 9px rgba(0, 252, 254, 0.5), inset 0px 4px 9px rgba(0, 252, 254, 0.5);
        border-radius: 99px;
        padding: 5px 15px;
    }
    .Navbar .user .profile .name {
        margin: 0;
    }

    .Navbar .pending {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .Navbar .pending .withdrawLabel {
        margin-right: 7px;
    }

@keyframes balanceIncrease {
    from {
        color: #F5F5F5;
    }
    50% {
        color: rgb(37, 185, 0);
    }
    to {
        color: #F5F5F5;
    }
}
@keyframes balanceDecrease {
    from {
        color: #F5F5F5;
    }
    50% {
        color: rgb(185, 37, 0);
    }
    to {
        color: #F5F5F5;
    }
}

@media (max-width: 880px) {
    .Navbar {
        display: flex;
        flex-direction: column;
        height: auto;
        position: relative;
    }
    .Navbar .user {
        display: none;
    }
    .Navbar .navbar {
        align-items: unset;
    }
    .Navbar .logo {
        border-right: none;
    }
    .Navbar .navLinks {
        display: none;
    }
    .Navbar .navLink {
        margin: 15px 0;
    }
    .Navbar .logo img {
        height: 50px;
    }
    .Navbar .mobile_user_wrapper {
        display: flex;
        justify-content: center;
        background: #1C1D25;
        height: 70px;
    }
    .Navbar .mobile_user {
        display: flex;
        align-items: center;
    }
    .Navbar .toggleMenu {
        margin-top: 5px;
    }
    .mobile_user .profile {
        margin-left: 10px;
    }
    .mobile_user .balance {
        display: flex;
        align-items: center;
        background: #141416;
        padding: 7px 15px;
        border-radius: 5px;
    }
    .mobile_user .balance .amount {
        margin: 0;
        margin-left: 5px;
    }
    .mobile_user .profile svg {
        width: 60px;
        height: 60px;
        object-fit: cover;
    }
    .mobile_user .withdrawB,
    .mobile_user .depositB {
        position: relative;
        display: block;
        border: none;
        height: 40px;
        width: 40px;
        background: #343a45;
        margin-left: 10px;
        border-radius: 5px;
    }

    .mobile_user .depositB::before {
        content: '';
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        height: 3px;
        width: 15px;
        background: #59BD7D;
        margin-left: -7.5px;
        margin-top: -1.5px;
    }
    .mobile_user .depositB::after {
        content: '';
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        width: 3px;
        height: 15px;
        background: #59BD7D;
        margin-top: -7.5px;
        margin-left: -1.5px;
    }

    .mobile_user .withdrawB::before {
        content: '';
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        height: 3px;
        width: 15px;
        background: #0188A7;
        margin-left: -7.5px;
        margin-top: -1.5px;
    }

    .multiButton {
        display: none;
    }
    .mobile_user .wallet-adapter-button-trigger {
        display: flex;
    }
}